import { Navbar } from '@/components/navbar'
import { getAnimeDetails, Anime, Episode } from '@/lib/api'
import { notFound } from 'next/navigation'
import Link from 'next/link'
import { Suspense } from 'react'

// Skeleton Grid Component
function FlexSkeletonGrid({ count }: { count: number }) {
  return (
    <div className="grid grid-cols-1 gap-4">
      {Array.from({ length: count }).map((_, i) => (
        <div key={i} className="h-20 bg-card/50 rounded-xl animate-pulse border border-border"></div>
      ))}
    </div>
  )
}


// Komponen List Episode
function EpisodeList({ episodes, animeId }: { episodes: Episode[], animeId: string | number }) {
  if (!episodes || episodes.length === 0) {
    return (
      <div className="text-center py-8 bg-card rounded-lg border border-border">
        <p className="text-muted-foreground">Tidak ada data episode tersedia untuk streaming.</p>
      </div>
    )
  }

  return (
    <div className="space-y-3">
      {episodes.map(episode => (
        <Link 
          key={episode.id}
          // Path untuk menonton episode
          href={`/watch/${animeId}/${episode.id}`} 
          className="flex items-center p-3 md:p-4 bg-card hover:bg-card/80 transition-colors duration-200 rounded-xl border border-border group"
        >
          <div className="flex-shrink-0 mr-4">
            <span className="text-lg font-semibold text-primary group-hover:text-primary-light transition-colors">
              EP {episode.number}
            </span>
          </div>
          <div className="flex-grow">
            <h3 className="text-foreground font-medium truncate">
              {episode.title || `Episode ${episode.number}`}
            </h3>
            <p className="text-sm text-muted-foreground hidden sm:block">
              {episode.description ? `${episode.description.substring(0, 80)}...` : 'Tonton episode terbaru.'}
            </p>
          </div>
          <svg className="w-5 h-5 text-primary group-hover:translate-x-1 transition-transform ml-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M14 5l7 7m0 0l-7 7m7-7H3"></path>
          </svg>
        </Link>
      ))}
    </div>
  )
}


export default async function AnimeDetailsPage({ params }: { params: { id: string } }) {
  const animeId = params.id

  const data = await getAnimeDetails(animeId)

  if (!data) {
    notFound()
  }

  const anime: Anime = data.data
  const episodes: Episode[] = data.episodes || []

  const coverImageUrl = anime.cover || anime.image || 'https://placehold.co/1200x600/1f2937/d1d5db?text=Anime+Cover'

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="relative">
        {/* Backdrop Image */}
        <div 
          className="h-[40vh] md:h-[60vh] bg-cover bg-center" 
          style={{ backgroundImage: `url(${coverImageUrl})` }}
        >
          <div className="absolute inset-0 bg-black/70 backdrop-blur-sm" />
        </div>

        {/* Content Section */}
        <div className="absolute top-0 left-0 w-full h-full pt-12">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex flex-col md:flex-row gap-8">
            {/* Poster */}
            <div className="w-full md:w-auto md:flex-shrink-0 -mt-16 md:-mt-24">
              <img
                src={anime.image}
                alt={anime.title}
                className="w-full max-w-[200px] md:max-w-[300px] aspect-[2/3] object-cover rounded-xl shadow-2xl border-4 border-card/50"
                onError={(e) => { e.currentTarget.onerror = null; e.currentTarget.src = 'https://placehold.co/300x450/374151/f3f4f6?text=No+Poster' }}
              />
            </div>

            {/* Details */}
            <div className="text-white md:pt-8">
              <h1 className="text-4xl md:text-5xl font-extrabold mb-3 leading-tight text-primary-light drop-shadow-lg">
                {anime.title}
              </h1>
              
              <div className="flex flex-wrap items-center gap-4 text-sm font-medium mb-6">
                <span className="bg-primary px-3 py-1 rounded-full shadow-md text-xs">
                  {anime.type || 'Series'}
                </span>
                <span className="text-yellow-400 flex items-center">
                  <svg className="w-5 h-5 mr-1" fill="currentColor" viewBox="0 0 20 20"><path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.817 2.053a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.817-2.053a1 1 0 00-1.175 0l-2.817 2.053c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72a1 1 0 01.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" /></svg>
                  {anime.rating || 'N/A'}
                </span>
                <span className="text-white/80">
                  {/* FIX: Menggunakan properti releaseDate yang benar */}
                  {anime.releaseDate || 'Unknown Date'}
                </span>
                <span className="text-white/80">
                  {anime.subOrDub || 'Sub/Dub'}
                </span>
              </div>

              <p className="text-white/90 leading-relaxed mb-6 max-w-2xl text-base md:text-lg">
                {anime.description}
              </p>

              <h3 className="text-xl font-semibold mb-3 text-primary-light">Genres</h3>
              <div className="flex flex-wrap gap-2 mb-8">
                {anime.genres?.map(genre => (
                  <span key={genre} className="bg-white/10 text-white/90 px-3 py-1 text-sm rounded-full border border-white/20">
                    {genre}
                  </span>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Episodes Section */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h2 className="text-3xl font-bold mb-6 text-foreground">Episodes ({episodes.length})</h2>
        <Suspense fallback={<FlexSkeletonGrid count={5} />}>
          <EpisodeList episodes={episodes} animeId={animeId} />
        </Suspense>
      </div>
    </main>
  )
}