// lib/api.ts

// âœ… BASE URL BARU: Consumet API (Provider Zoro)
const ZORO_API_BASE = 'https://api.consumet.org/anime/zoro'

// --- 1. UPDATED INTERFACE (DISINKRONKAN DENGAN JIKAN/ZORO) ---
export interface Anime {
    released: string;
    // Properti Wajib untuk Frontend dan Pemetaan
    mal_id: number; // Digunakan sebagai ID unik di frontend
    title: string;
    images: {
        jpg: {
            image_url: string;
            small_image_url: string;
            large_image_url: string;
        }
    };
    
    // Properti Tambahan Zoro/Jikan
    title_english?: string;
    synopsis?: string;
    episodes?: string | number | null; // Tipe Zoro seringkali string
    status?: string;
    score?: number | null;
    genres?: Array<{ name: string; id?: string }>; // Zoro menggunakan string nama genre
    studios?: Array<{ mal_id: number; name: string }>; // Tidak tersedia di Zoro, dipertahankan sebagai placeholder
    year?: number | null; // Tidak tersedia di Zoro
    aired?: { // Tidak tersedia di Zoro
        from: string;
        to?: string | null;
    };
    
    // Properti Kunci Zoro/Consumet (untuk internal)
    animeId?: string; 
}

// Response interface (dipertahankan untuk kompatibilitas fungsi)
export interface AnimeResponse {
    data: Anime[];
    pagination?: {
        last_visible_page: number;
        has_next_page: boolean;
        current_page: number;
    };
}

// Utility function untuk konversi Image dari URL string ke objek nested
const mapImage = (imageUrl: string) => ({
    images: {
        jpg: {
            image_url: imageUrl,
            small_image_url: imageUrl,
            large_image_url: imageUrl,
        }
    }
});

// Utility function untuk konversi Genre dari string[] ke objek[]
const mapGenres = (genres: string[] | undefined): Anime['genres'] => {
    return genres ? genres.map(name => ({ name })) : [];
};


// Optional rate limit helper (Dihapus karena Consumet tidak membatasi secepat Jikan)
async function rateLimit() {
    return new Promise((resolve) => setTimeout(resolve, 0)) // 0ms timeout
}

// --- FUNGSI BARU ZORO ---

// Fetch top anime (Mapping ke /recent-episodes)
export async function getTopAnime(page = 1): Promise<AnimeResponse> {
    try {
        await rateLimit();
        // Consumet tidak mendukung parameter page pada endpoint ini, ambil saja 50 item
        const res = await fetch(`${ZORO_API_BASE}/recent-episodes?type=1&page=${page}`);
        if (!res.ok) throw new Error('Failed to fetch recent anime (Zoro)');
        const data = await res.json();
        
        const mappedData: Anime[] = data.results.map((item: any) => ({
            mal_id: 0, // Set 0 karena data Zoro tidak selalu memiliki MAL ID
            animeId: item.id,
            title: item.title,
            episodes: item.episodeNumber,
            score: null,
            genres: [],
            ...mapImage(item.image),
        }));
        
        return { data: mappedData, pagination: { last_visible_page: 1, has_next_page: false, current_page: 1 } };
    } catch (error) {
        console.error('Error fetching top anime (Zoro):', error);
        return { data: [] };
    }
}

// Fetch anime detail by ID (Menggunakan endpoint /info)
export async function getAnimeDetail(id: number | string): Promise<Anime | null> {
    try {
        await rateLimit();
        // Menggunakan ID sebagai parameter query
        const res = await fetch(`${ZORO_API_BASE}/info?id=${id}`);
        if (!res.ok) throw new Error('Failed to fetch anime detail');
        const data = await res.json();
        
        return {
            mal_id: data.mal_id || 0, // Coba ambil mal_id
            animeId: data.id,
            title: data.title,
            title_english: data.otherName,
            synopsis: data.description,
            episodes: data.totalEpisodes,
            status: data.status,
            score: data.rating ? parseFloat(data.rating) : null,
            genres: mapGenres(data.genres),
            year: data.releaseDate ? parseInt(data.releaseDate) : null,
            ...mapImage(data.image),
        };
    } catch (error) {
        console.error('Error fetching anime detail (Zoro):', error);
        return null;
    }
}

// Fetch popular anime (Menggunakan endpoint yang sama dengan TopAnime untuk kesederhanaan)
export async function getPopularAnime(page = 1): Promise<AnimeResponse> {
    // Di Zoro, top-airing atau recent-episodes adalah list utama.
    // Kita panggil ulang fungsi getTopAnime untuk kesederhanaan.
    return getTopAnime(page);
}

