// Search anime (Menggunakan endpoint /search)
export async function searchAnime(query: string, page = 1): Promise<AnimeResponse> {
    try {
        await rateLimit();
        const url = `${ZORO_API_BASE}/search?query=${encodeURIComponent(query)}&page=${page}`;
        const res = await fetch(url);

        if (!res.ok) {
            const text = await res.text();
            console.error('Failed search response (Zoro):', text);
            throw new Error(`Failed to search anime: ${res.status} ${res.statusText}`);
        }

        const data = await res.json();
        
        const mappedData: Anime[] = data.results.map((item: any) => ({
            mal_id: item.mal_id || 0,
            animeId: item.id,
            title: item.title,
            episodes: item.totalEpisodes || item.episodes,
            score: null,
            genres: mapGenres(item.genres),
            ...mapImage(item.image),
        }));
        
        return { data: mappedData, pagination: { last_visible_page: 1, has_next_page: false, current_page: 1 } };
    } catch (error) {
        console.error('Error searching anime (Zoro):', error);
        return { data: [] };
    }
}

// Get genres (Dihapus karena Zoro tidak punya endpoint ini)
